@import url(https://fonts.googleapis.com/css?family=Cute+Font);
@import url(https://fonts.googleapis.com/css?family=Lato|Roboto);
* {
    border: 0;
    padding: 0;
    margin: 0;
}

section {    
    position: relative;
    display: flex;
    color: white;
    width: 100%;
    height: 100vh;
    background-attachment: fixed;
    background-size: 100% 100vh;
}

section h2 {
    font-family: "Cute Font";
    font-size: 100px;
}

#description {
    position: absolute;
    top: 20%;
    left: 5%;
}

#description .overview {
    width: 30%;
    color: white;
    font-size: 16px;
}

#description .trailer {
    display: inline-block;
    line-height: 20px;
    vertical-align: top;
    padding: 12px;
}

#description .fa-youtube {
    color: rgba(255, 255, 255, 0.85); 
    text-decoration: none;
    font-size: 45px;
}

.tooLarge {
    font-size: 60px !important;
}
#navbar {
    position:  fixed;
    top: 0;
    width: 100%;
    z-index: 4;
    background-color: rgb(28, 27, 27);
    color: rgb(160, 154, 154);
}

#home {
    color: rgb(160, 154, 154);
}

#navbar ul {
    margin:0;
    padding: 0;
    list-style: none;
}

#navbar ul li {
    float: left;
    width: 100px; /*padding maybe */
    line-height: 40px;
    text-align: center;
    height: 38px;
}

#navbar ul li a {
    text-decoration: none;
    color: black; 
    display: block;
}

#navbar ul li a:hover {
    font-size: larger;
}

#navbar ul li ul li {
    display: none;
}

#navbar ul li ul li a {
    width: 100px;
    color: rgb(160, 154, 154);
    background-color: rgb(28, 27, 27);
    border-bottom: 6px solid black;
}

#navbar ul li:hover ul li {
    display: block;
}

#navbar i {
    float: right;
    padding: 14px 16px;
    font-size: 17px;
    color: white;
}

#navbar i:hover {
    cursor: pointer;
}

.searchInput form {
    float: right;
    margin-top: 1vh;
    margin-right: 3vw;
}

input#input:focus {
   width: 400px;
}

input#input::-webkit-input-placeholder {
    padding: 2px;
}

input#input::-moz-placeholder {
    padding: 2px;
}

input#input::-ms-input-placeholder {
    padding: 2px;
}

input#input::placeholder {
    padding: 2px;
}
.related {
    margin-top: 125px;
    background-color: rgba(21, 21, 21, .7);
    padding: 20px;
    border-top: 2px solid rgba(21, 21, 21, .7);
}

.related div h1{
    color: whitesmoke;
}

.relatedFilm img {
    height: 190px;
    width: 20vh;
}
.slick-slide {
      width: auto !important;
      margin: 0;
      padding: 0;
}

.slider {
      position: relative;
}

.slick-prev, .slick-next {
      position: absolute;
      top: 50%;
      height: 40px;
      width: 40px;
      font-size: 40px;
      z-index: 30;
}

.slick-prev {
      left: -10%;
}

.slick-next {
      right: -10%;
}

.slick-cloned {
      /* display: none !important; */
}
.options {
    background-color: rgb(28, 27, 27);
    color: rgb(160, 154, 154);
    width: 100;
    height: 5vh;
    cursor: pointer;
}

.options ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.options li {
    display: inline;
    text-decoration: none;
}

.options li:hover {
    cursor: pointer;
}

#selectedBackDrop {
    width: 100%;
    height: 98vh;
    position: fixed;
    top: 3vh;
    z-index: -1;
    filter: blur(4px);
    -webkit-filter: blur(4px);
}

#selectedPoster {
    height: auto;
    width: 55vh;
    margin-top: 10vh;
}

#selectedBackDrop {
    -webkit-filter: brightness(50%);
            filter: brightness(50%);
}

.selectedDesciption h1 {
    text-align: center;
    margin-bottom: 5vh;
    margin-top: 15vh;
}

#overview {
    height: 170px;
    overflow: scroll;
}

.starRating {
    color: rgb(239, 239, 50);
    font-size: 23px;
}

#director {
    font-size: 20px;
}

.rating {
    color: white;
}

#trailer {
    margin-top: 50px;
    width: 80vw;
    height: 70vh;
}

#details {
    color: white;
}

.castMember {
    position: relative; 
    display: inline-block;
}

.castMember img {
    display: block;
    width: 100px !important;
    height: 150px !important;
}

.castMember .name {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100%;
    width: 100%;
    opacity: 0;
}

.castMember:hover img  {
    opacity: .3;
}

.castMember:hover .name  {
    opacity: 1;
}

.character .performer {
    color: white;
    font-size: 5px;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    text-align: center;
}
body {
    margin: 0;
    padding: 0;
}

.mediaPoster {
    width: 230px;
    height: 338px;
    margin-right: 20px;
}

.mediaTitle {
    font-size: 15px;
    text-align: center;
}

#group {
    text-align: center;
    color: black;
    font-family: Roboto;
    margin-top: 10vh;
    margin-bottom: 22px;
}

.favs h2 {
    text-decoration: none;
    color: black;
    width: 230px;
    margin-bottom: 14px;
    font-size: 20px;
    font-family: Roboto;
}
#related {
    position: absolute;
    top: 100vh;
    width: 100%;
}
